<template>
	<view>
		<view class="header"
		:style="[{backgroundColor:'#ffff'+m_header_opacity},{boxShadow: '6rpx 6rpx 15rpx 3rpx #0000'+m_header_shadow}]">
			
			<view class="wrapper_search trans" 
				@click="navigate('/pages/search/search')"
				:style="[{marginTop:m_top},{width:m_left}]">
				<u-search :show-action="false" 
					clearabled="false" disabled="false" 
					placeholder="搜索" 
					v-model="m_search" shape="round"
					:bg-color="m_header_color">
				</u-search>
			</view>
		</view>
		<view class="panel card">
			<view class="entry trans" 
				v-for="(item,index) in entry"
				:key="index"
				@click="navigate(item.navigation)"
			>
				<view :class="'entry_icon psl_font '+item.icon"></view>
				<view class="entry">{{item.entry}}</view>
			</view>
		</view>
		
		<view class="wrap">
			<view class="wrap_title">
				精选
				<span class="wrap_title_info">
					使用次数越多，精选越合乎心意
				</span>
			</view>
			<view class="u-tabs-box"
			:style="[{backgroundColor:'#ffff'+m_tab_opacity},{boxShadow: '6rpx 6rpx 15rpx 3rpx #0000'+m_tab_shadow}]">
				<u-tabs-swiper :bgColor="m_tab_bgcolor" activeColor="#007aff" ref="tabs" :list="tab_list" :current="current"
				 @change="change" :is-scroll="false" swiperWidth="750"></u-tabs-swiper>
			</view>
			<swiper class="swiper-box" :current="swiperCurrent" @transition="transition" @animationfinish="animationfinish">
				<swiper-item class="swiper-item">
					<scroll-view scroll-y style="height: 100%;width: 100%;" @scrolltoupper="reachTop" @scrolltolower="reachBottom">
						<view
							v-for="(projectItem,projectIndex) in index_project"
							:key="projectIndex">
							<team-project :projectItem="projectItem">
							</team-project>
						</view>
						<u-loadmore :status="loadStatus[0]" bgColor="transparent"></u-loadmore>
					</scroll-view>
				</swiper-item>
				<swiper-item class="swiper-item">
					<scroll-view scroll-y style="height: 100%;width: 100%;" @scrolltolower="reachBottom">
						<view v-for="(talentItem,talentIndex) in index_talent" :key="talentIndex">
							<team-talent :talentItem="talentItem">
							</team-talent>
						</view>
						<u-loadmore :status="loadStatus[1]" bgColor="transparent"></u-loadmore>
					</scroll-view>
				</swiper-item>
				<swiper-item class="swiper-item">
					<scroll-view scroll-y style="height: 100%;width: 100%;">
						<view v-for="(qaItem, qaIndex) in index_qa" :key="qaIndex">
							<team-qa :qaItem="qaItem"></team-qa>
						</view>
						<u-loadmore :status="loadStatus[2]" bgColor="transparent"></u-loadmore>
					</scroll-view>
				</swiper-item>
			</swiper>
		</view>
	</view>
</template>

<script>
	import aliasAvatar from "@/components/aliasAvatar/aliasAvatar.vue"
	var m_this;
	import {
		index_data_refresh
	} from "@/api1/mock.js";
	
	import teamProject from "@/components/team-project/team-project.vue";
	import teamTalent from "@/components/team-talent/team-talent.vue";
	import teamQa from "@/components/team-qa/team-qa.vue";
	export default {
		components: {
			aliasAvatar,
			teamProject,
			teamTalent,
			teamQa
		},
		data() {
			return {
				entry:[
					{entry:"项目圈",icon:"icon-emailChange",navigation:"/pages/project/project"},
					{entry:"人才圈",icon:"icon-emailChange",navigation:"/pages/talent/talent"},
					{entry:"问答圈",icon:"icon-emailChange",navigation:"/pages/qa/qa"},
					{entry:"竞赛圈",icon:"icon-emailChange",navigation:"/pages/contest/contest"},
					
				],
				tabsHeight: 0,
				dx: 0,
				loadStatus: ['loadmore', 'loadmore', 'loadmore', 'loadmore'],

				m_height: "100px",
				scrollHeight: 0,

				current: 0, // tabs组件的current值，表示当前活动的tab选项
				swiperCurrent: 0, // swiper组件的current值，表示当前那个swiper-item是活动的
				tab_list: [{
					name: '项目圈',
					num: 1
				}, {
					name: '人才圈',
					num: 1
				}, {
					name: '问答圈',
					num: 1
				}],
				m_search: "",
				m_alias: 'RyanAlexander',
				m_avatar: 'http://sayhitotheworld.ryanalexander.cn/team/talent/ryan.jpg',
				m_top: 0,
				m_width: 0,
				m_left: 0,
				m_height_header: 30,
				m_header_shadow: '0000',
				m_header_opacity: '0000',
				m_tab_shadow: '0000',
				m_tab_opacity: '0000',
				m_tab_bgcolor: "transparent",
				m_header_color: '#ffffff',
				m_header_middle_msg: 9,

				m_uid: 0,
				m_access: '',
				m_news_navigate: '/pages/news/news?',

				index_project: [],

				index_talent: [],
				// 首先  可以选择是否展示自己头像和组织等等 有些名人需要用自己的名人效应来增加问题被回答的可能性
				// 另外  高分回答也是为了抛砖引玉 至于显示不显示回答者的头像那些 也由他们选择 默认是有的 
				// 注意  两者最好区分一下 否则会造成意义不明 到底是提问者还是回答者
				index_qa: [],
			};
		},
		onLoad() {
			m_this = this;
			this.m_height = uni.getSystemInfoSync().windowHeight

			this.m_left = (uni.getStorageSync("left") - 26) + 'px';

			this.m_width = uni.getStorageSync("width");

			this.m_top = (uni.getStorageSync("top") - this.m_width / 2) + 'px';

			this.m_uid = uni.getStorageSync("uid");
			this.m_access = uni.getStorageSync("a");
			index_data_refresh(0,3).then(function(value){
				m_this.index_project=value;
			});
			
			index_data_refresh(1,4).then(function(value){
				m_this.index_talent=value;
			});
			index_data_refresh(2,4).then(function(value){
				m_this.index_qa=value;
			});
			
			// this.getOrderList(0);
			// this.getOrderList(1);
			// this.getOrderList(3);
		},
		onPageScroll: function(e) {
			console.log(e.scrollTop)
			
			if (e.scrollTop == 0) {
				this.m_header_shadow = '0000'
				this.m_header_opacity = '0000'
				this.m_header_color = '#ffffff'
				this.m_tab_shadow = '0000'
				this.m_tab_opacity =  '0000'
				this.m_tab_bgcolor =  "transparent"
				
			} 
			else if(e.scrollTop >= 124){
				this.m_header_shadow = '0000'
				this.m_header_opacity = 'ffff';
				this.m_tab_shadow = '0020'
				this.m_tab_opacity = 'ffff'
				this.m_tab_bgcolor = "#ffffff"
			}
			else {
				this.m_header_opacity = 'ffff';
				this.m_header_shadow = '0020';
				this.m_header_color = '#F8FAFC';
				this.m_tab_shadow = '0020'
				this.m_tab_opacity = 'ffff'
				this.m_tab_bgcolor = "#ffffff"
			}
		},

		methods: {
			reachTop() {
			},
			reachBottom() {
				
				let index = [this.index_project,this.index_talent,this.index_qa];
				this.loadStatus.splice(this.current,1,"loading")
				index_data_refresh(this.current,2).then(function(value){
					index[m_this.current].push.apply(index[m_this.current],value);
					console.log(index[m_this.current])
					console.log(m_this.current)
					m_this.loadStatus.splice(m_this.current,1,"loadmore")
				});
				
			},
			
			// swiper-item左右移动，通知tabs的滑块跟随移动

			// transition(e) {
			// 	let dx = e.detail.dx;
			// 	this.$refs.uTabs.setDx(dx);
			// },

			// 由于swiper的内部机制问题，快速切换swiper不会触发dx的连续变化，需要在结束时重置状态
			// swiper滑动结束，分别设置tabs和swiper的状态
			// tab栏切换
			change(index) {
				this.swiperCurrent = index;
				// this.current = index;
				// 这个不能加 否则有bug
				
			},
			transition({detail: {dx}}) {
				this.$refs.tabs.setDx(dx);
			},
			animationfinish({
				detail: {
					current
				}
			}) {
				this.$refs.tabs.setFinishCurrent(current);
				this.swiperCurrent = current;
				this.current = current;
			},
			navigate(navigation) {
				uni.navigateTo({
					url: navigation,
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			}


		}
	};
</script>

<style>
	page {
		height: 100vh;
		/* background-color: #ECF5FE; */
		background: linear-gradient(to bottom,  #ECF5FE 0%,#ffffff 70%);
	}
</style>

<style lang="less">
	@import "@/common/uni.less";
	@import "./index.less";
</style>


<style lang="scss" scoped>
	@import "@/common/uni.scss";
	$heightHeader: 96px;
	.panel{
		margin: $padding;
		margin-top: $heightHeader;
		position: relative;
		width: auto;
		height: $heightHeader+4px;
		padding: $padding;
		flex-direction: row;
		display: flex;
		justify-content: space-around;
		align-content: center;
		.entry{
			display: flex;
			flex-direction: column;
			align-content: center;
			.entry_icon{
				font-size: 50px;
				color: $themeColor;
			}
			.entry{
				color: $labelColor2;
				text-align: center;
				line-height: 20px;
				font-size: 13px;
				height: 20px;
			}
		}
		.entry:active{
			opacity: 0.1;
		}
		
	}
	
	
	.wrap {
		background-color: $cardColor;
		border-radius: $padding;
		
		.wrap_title{
			font-size: 24px;
			margin-left: $padding;
			margin-top: $padding;
			font-weight: bolder;
			color: $labelColor2;
			.wrap_title_info{
				color: $labelColor;
				margin-left: $padding/2;
				font-size: 10px;
				font-weight: normal;
			}
		}
		.u-tabs-box {
			z-index:99;
			top: $heightHeader;
			width: 100vw;
			position: sticky;
			
		}
		// margin-top: $heightHeader;
		display: flex;
		flex-direction: column;
		height: calc(100vh - var(--window-top));
		width: 100%;
		.swiper-box {
			flex: 1;
			.swiper-item {
				height: 100%;
			}
		}
	}

	

	
</style>
